<template>
  <div id="show-blogs">
      <h1>微博总览how</h1>
      <input type="text" v-model="search" placeholder="搜索">
      <div v-for="blog in filtereBlog" id="single-blog">
        <router-link v-bind:to="'/blog/' + blog.id"><h2 v-rainbow>{{ blog.title | to-uppercase }}</h2></router-link>
        <article>{{ blog.content }}</article>
      </div>
  </div>
</template>

<script>
export default {
  name: 'show-blogs',
  data(){
    return{
        blogs:[],
        search:''
    }
  },
  created(){
    this.$http.get('https://vuedemo-43cf2.firebaseio.com/posts.json')
    .then(
      function(data){
        //处理从数据库返回的json对象
        return data.json()
        // this.blogs = data.body.slice(0,10);
        // console.log(this.blogs)


      }
    ).then(function(data){
      var blogsArray = [];
      for(let key in data){
        console.log(key)
        data[key].id = key;
        blogsArray.push(data[key])
        
      }
      console.log(blogsArray);
      this.blogs = blogsArray
    })
  },
  computed:{
    filtereBlog:function(){
      return this.blogs.filter((blog)=>{
        return blog.title.match(this.search)
      })
    }
  },
  filters:{
    "to-uppercase":function(value){
      return value.toUpperCase();
    }
  }
}
</script>

<style >
#show-blogs{
  max-width: 800px;
  margin: 0 auto;
}
#single-blog{
  padding: 20px;
  margin:10px 0;
  /* margin-top: 10px; */
  box-sizing:border-box;
  background:#eee;
  border:1px dotted #aaa;
}
#show-blogs a{
  color:#444;
  text-decoration: none;
}
input[type = "text"]{
  padding:8px;
  width:100%;
  box-sizing: border-box;
  border-radius:10px;
}
</style>



