<template>
  <div id="add-blog">
   <h1>添加博客</h1>
   <form v-if="!sumbit">
     <label>博客标题</label>
     <input type="text" v-model="blog.title" >
     <label >博客正文</label>
     <textarea v-model="blog.content"></textarea>
     <div id="checkboxes">
       <label >Vue.js</label>
       <input type="checkbox" value="Vue.js" v-model="blog.categories">
       <label >Node.js</label>
       <input type="checkbox" value="Node.js" v-model="blog.categories">
       <label >React.js</label>
       <input type="checkbox" value="React.js" v-model="blog.categories">
       <label >Angular4</label>
       <input type="checkbox" value="Angular4" v-model="blog.categories">
     </div>
     <label >作者：</label>
     <select v-model="blog.author">
       <option v-for="author in authors">{{ author }}</option>
     </select>
     <button v-on:click.prevent="post">发布博客</button>
   </form>
   <hr>
   <div id="preview">
     <h3 v-if="sumbit">博客发表成功</h3>
     <h3>博客总览</h3>
     <p>博客标题:{{ blog.title }}</p>
     <p>博客正文</p>
     <p>{{ blog.content }}</p>
     <p>博客分类:</p>
     <ul>
       <li v-for="cetegory in blog.categories" >{{ cetegory }}</li>
     </ul>
     <p>作者:{{ blog.author }}</p>
   </div>
  </div>
</template>

<script>
export default {
  name: 'add-blog',
  data () {
    return {
      blog:{
        title:"请输入标题",
        content:"请输入正文",
        categories:[],
        author:""
      },
      authors:["游客","用户","管理员"],
      sumbit:false
    }
  },
  methods:{
    post:function(){
      this.$http.post(
        "https://vuedemo-43cf2.firebaseio.com/posts.json",this.blog
      )
        .then(function(data){
          console.log(data);
          this.sumbit = true;
        })
    }
  }
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#add-blog *{
  box-sizing: border-box;
  
}
#add-blog{
  margin: 0px auto;
  max-width: 600px;
  padding: 20px;
  vertical-align: left;
}

label{
  display: block;
  margin: 20px 0 10px;
  align-self: auto;
}
input[type="text"],textarea,select{
  display: inline-block;
  width: 100%;
  padding: 8px

}

textarea{
  height: 200px;
}
#checkboxes label{
  display: inline-block;
  margin-top:0;
}
#checkboxes input{
  display: inline-block;
  margin-right: 10px;
  text-align: left;
}
button{
  display: block;
  margin: 20px 0;
  background:crimson;
  color:#fff;
  border: 0;
  padding: 14px;
  border-radius: 4px;
  font-size: 18px;
  cursor:pointer;

}
#preview{
  padding: 10px 20px;
  border: 1px dotted #ccc;
  margin: 30px 0;

}
h3{
  margin-top: 10px;
}
</style>



